.navbar {
    background-color: hwb(60 13% 8%);
    color: #12d95b;
    padding: 10px 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .logo {
    font-size: 1.5rem;
  }
  
  .actions button {
    background-color: hsl(321, 70%, 54%);
    color: hsl(161, 80%, 41%);
    
    border: none;
    padding: 8px 16px;
    margin-left: 10px;
    cursor: pointer;
  }
  
  .actions button:hover {
    background-color: #e05712;
  }
  
  .actions div {
    display: flex;
    align-items: center;
  }
  
  .actions div button {
    margin-left: 10px;
  }